@tailwind base;
@import './base.css';

@tailwind components;
@tailwind utilities;

/* Chrome, Safari, Edge, Opera */
.alpha-chat-widget input::-webkit-outer-spin-button,
.alpha-chat-widget input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
.alpha-chat-widget input[type='number'] {
  -moz-appearance: textfield;
}

.alpha-chat-widget *,
.alpha-chat-widget *:before,
.alpha-chat-widget *:after {
  box-sizing: inherit;
}

.alpha-chat-widget {
  @apply tw-font-sans tw-text-black tw-bg-white;

  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overscroll-behavior-x: none;
}

.alpha-chat-widget a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.alpha-chat-widget .animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.alpha-chat-widget .fadeIn {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
